import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../Assets/AssetsHelper';
import { MEDIA_REPLACE_URL } from '../../../../Services/Utils/utils'
import ProfileImage from '../../CommonComponents/ProfileImage/ProfileImage';

class ProfileOverview extends Component {

    render() {
        const { profile, userData } = this.props
        return userData && Object.keys(userData).length > 0 ? <div className="container container-xs">
            <div className="mt-5">
                <h3 className="fs-6 text-gray-800">Profile Overview</h3>
            </div>
            <div className="card mb-3 mb-xxl-4">
                <div className="card-body p-3 d-flex justify-content-between align-items-start flex-wrap">
                    <div className="d-flex align-items-center flex-wrap flex-grow-1">
                        <div className="me-3 me-xxl-4">
                            <ProfileImage className={'symbol h-90px w-90px position-relative rounded-circle overflow-hidden bg-light'}
                                imageSize={90}
                                symbolClass={'symbol-label text-light-alt fw-bold display-6 text-uppercase'}
                                name={userData.name} profileUrl={MEDIA_REPLACE_URL(userData.picture)} networkId={0} />
                        </div>
                        <div className="d-flex flex-column my-2">
                            <div className="d-flex align-items-center mb-2">
                                <h6 className="fs-4 text-dark text-hover-primary mb-0 me-1">{userData.name}</h6>
                                {userData.access == 1 ? <span className="svg-icon svg-icon-1 svg-icon-primary">
                                    <SVG src={baseUrl('media/icons/svg-icons/check-pattern.svg')} height={20} width={20} />
                                </span> : null}
                            </div>
                            <div className="d-flex flex-wrap pe-2">
                                <p className="d-flex align-items-center text-decoration-none text-hover-primary mb-2 fw-medium">
                                    <i className="ri-mail-line icon-6 text-dark me-1"></i>
                                    {userData.emailId}
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="d-flex mt-4">
                        {profile === 'view' ?
                            <Link to={'/profile/edit'} className="btn btn-primary btn-sm me-2">Edit Profile</Link> :
                            <Link to={'/profile'} className="btn btn-primary btn-sm me-2" >View Profile</Link>}
                    </div>
                </div>
            </div>
        </div> : null

    }
}

export default ProfileOverview;